body {
    background-color: black;
}

.main-content-wrap-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 80vw;
    height: 100vh;
    gap: 5rem;
    height: max-content;
    overflow: hidden;
}

@media (max-width : 800px) {
    .main-content-wrap-container {
        width: 95vw !important;
    }
}

.custom-shape-divider-top-1683437920>svg {
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
    background-color: #12181B;
}

.custom-shape-divider-top-1683437920 {
    width: 100%;
    height: 500px;
    background-color: #12181B;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.custom-shape-divider-top-1683437920 .shape-fill {
    fill: black;
}

.footer-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    gap: 1rem;
}

.footer-links {
    display: flex;
    gap: 1rem;
}

.footer-link {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 1.8rem;
}

.email {
    font-family: 'Cubano';
    color: #ADB9C8;
}

.copyright {
    color: #ADB9C8;
    font-size: .8rem;
}